.footer__middle {
  padding: 120px 0;
  background-color: #2a2c2b;
  color: rgba(255, 255, 255, 0.5);
}

.footer__logo {
  margin-bottom: 25px;
}

.footer__social-title {
  text-transform: uppercase;
  margin-bottom: 15px;
}

.footer__social ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  max-width: 150px;
}

.footer__social li {
  margin-right: 10px;
  margin-bottom: 10px;
}

.footer__social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 36px;
  border-radius: 5px;
  color: #ffffff;
  transition: 0.25s;
}

.footer__social-link--vk {
  background-color: #547bcf;
}

.footer__social-link--fb {
  background-color: #3b5998;
}

.footer__social-link--youtube {
  background-color: #df4b38;
}

.footer__social-link--instargam {
  background-image: linear-gradient(-141deg, rgb(141, 22, 206) 10%, rgb(220, 30, 163) 46%, rgb(255, 201, 0) 100%);
}

.footer__social-link--ok {
  background-color: #fb8821;
}

.footer__social-link--tw {
  background-color: #31b7e9;
}

.footer__social-link:hover {
  color: inherit;
}

.footer__menu-title {
  color: #ffffff;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.footer__menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer__menu li:not(:last-child) {
  margin-bottom: 15px;
}

.footer__menu a {
  color: inherit;
}

.footer__contacts {
  width: 195px;
  margin-left: auto;
}

.footer__contacts-btn {
  margin-bottom: 35px;
}

.footer__contact-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.footer__contact-item:last-child {
  margin-bottom: 0;
}

.footer__contact-item-icon {
  width: 22px;
  flex-shrink: 0;
  color: $success;
  position: relative;
  top: -2px;
}

.footer__bottom {
  padding: 35px 0 20px;
  background-color: #1c1d1c;
  color: rgba(255, 255, 255, 0.8);
}

.footer__bottom-development {
  text-align: right;
}
